@media (max-width: 782px) {
  .Res.S.Center {
    text-align: center;
  }
}

strong {
  font-weight: 700;
}

.wp-block-cover.HeroVideo {
  height: 100vh;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}
.wp-block-cover.HeroVideo .wp-block-cover__inner-container {
  height: 100%;
}
.wp-block-cover.HeroVideo .wp-block-cover__inner-container .wp-block-cover .wp-block-cover__background {
  z-index: 0;
}
.wp-block-cover.HeroVideo .wp-block-cover__inner-container .wp-block-cover video.wp-block-cover__video-background {
  z-index: 1;
  mix-blend-mode: screen;
}
.wp-block-cover.HeroVideo .wp-block-cover__inner-container .wp-block-cover .wp-block-cover__inner-container {
  z-index: 2;
}

.wp-block-cover.Section {
  height: 100vh;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}
.wp-block-cover.Section .wp-block-cover__inner-container {
  height: 100%;
}
.wp-block-cover.Section .wp-block-cover__inner-container .wp-block-cover {
  height: 100%;
}
.wp-block-cover.Section .wp-block-cover__inner-container .wp-block-cover .wp-block-group {
  height: 100%;
}

/**************************************
  MIXIN 
***************************************
*/
html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
}

body {
  -webkit-user-select: none;
  -ms-user-select: none;
}

.wp-site-blocks,
#APP,
.entry-content.wp-block-post-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0;
  margin: 0;
}

#APP {
  z-index: 0;
}

.entry-content.wp-block-post-content {
  overflow: auto;
  z-index: 1;
  background-color: transparent;
  isolation: isolate;
}